<template>
  <v-container
    id="listarUsuarios"
    fluid
    tag="section"
  >
    <v-btn
      to="/usuarios/nuevo"
      fab
      dark
      color="purple"
    >
      <v-icon>mdi-plus</v-icon>
    </v-btn>

    <base-material-card
      icon="mdi-clipboard-text"
      title="Registro de Usuarios"
      class="px-5 py-3"
    >
      <v-simple-table
        fixed-header
        class="elevation-3 my-5"
      >
        <thead>
          <tr>
            <th>ID</th>
            <th>Usuario</th>
            <th>Correo</th>
            <th>Cargo</th>
            <th>Acciones</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="user in users"
            :key="user._id"
          >
            <td>{{ user._id }}</td>
            <td>{{ user.username }}</td>
            <td>{{ user.email }}</td>
            <td>{{ user.roles.name }}</td>
            <td>
              <v-btn
                :to="{ name: 'Actualizar Usuario', params: { id: user._id } }"
                fab
                small
                color="light-blue"
              >
                <v-icon>mdi-pencil</v-icon>
              </v-btn>
              <v-btn
                fab
                small
                color="red"
                class="mx-5"
                @click.stop="dialog = true"
                @click="id = user._id"
              >
                <v-icon>mdi-delete</v-icon>
              </v-btn>
            </td>
          </tr>
        </tbody>
      </v-simple-table>
      <template>
        <div class="text-center">
          <v-pagination
            v-model="page"
            :length="4"
            circle
          />
        </div>
      </template>
    </base-material-card>

    <v-dialog
      v-model="dialog"
      width="350"
    >
      <v-card>
        <v-card-title class="headline grey lighten-2">
          ¿Desea eliminar el registro?
        </v-card-title>

        <v-divider />

        <v-card-actions>
          <v-spacer />
          <v-btn @click="dialog = false">
            Cancelar
          </v-btn>
          <v-btn
            color="error"
            @click="confirmarBorrado(id)"
          >
            Aceptar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
  import axios from 'axios'

  const urlUsuario = 'https://sistema-adv.herokuapp.com/api/users/'

  export default {
    name: 'ListarUsuarios',
    data () {
      return {
        dialog: false,
        users: null,
        page: 1,
      }
    },
    mounted () {
      this.obtenerUsuarios()
    },
    methods: {
      obtenerUsuarios () {
        axios
          .get(urlUsuario)
          .then((response) => {
            // console.log(response);
            this.users = response.data
          })
          .catch((err) => {
            console.log(err)
          })
      },
      confirmarBorrado (id) {
        axios
          .delete(urlUsuario + id)
          .then((res) => {
            this.obtenerUsuarios()
            this.dialog = false
          })
          .catch((e) => {
            console.log(e)
          })
      },
    },
  }
</script>
